<template>
  <div style="width: 1200px; margin: 0 auto;">
    <ul class="getJsonDemo">
      <li v-for="article in articles">
        <h4 class="tx-c" v-text="article.title"></h4>
        <img :src="getImage(article.images.medium)"/>
      </li>
    </ul>
    <!--<a href="#/map">111</a>-->
    <span style="margin: 10px 20px 10px 20px" v-for="titles in moviesTitle">
      {{titles.title}}
    </span>
    <br/>
    <p style="white-space: pre">
      1. : let [a,b,c] = [1,2,3]
      2.  for(let i of 'abc'){console.log(i)}; //'a','b','c'
      3.  'abc'.charAt(0) //'a'
      4. 
          let s = 'hello world';
          s.startsWith('hello') //true
          s.endsWith('i') //true
          s.includes('o') //true
          
          s.startsWidth('world', 6); //true
          s.endsWith('hello', 5); //true
          s.includes('hello', 6); //false
      5. repeatn,
          'x'.repeat(3) //'xxx'
      6. 
          '12'.padStart(10, '0') //0000000012
         .
          '12'.padStart(10, 'YYYY-MM-DD');
      7. 
          $("#result").append(`
            There are &lt;b&gt;${basket.count}&lt;/b&gt; items in your basket
          `);
      8. 
          let template = `
            &lt;ul&gt;
                  <% for(let i=0; i < data.supplies.length; i++) { %>
                        &lt;li&gt;<%= data.supplies[i] %>&lt;/li&gt;
                  <% } %>
            &lt;/ul&gt;
          `;
      9. Number.isInteger(),truefalse
         Number.parseInt('12.14') //12
         Number.parseFloat('123.45#') //123.45
      10. Math.sign()true1-1falsenull0
      11. Math.chrt() Math.cbrt(8) //2
      12. Math.hypot()  Math.hypot(3,4); //5
      13. 
          function logs(x, y = 'world'){
            console.log(x,y)
          };
          logs('hello') //hello world
          logs('hello','china') //hellow china
    </p>
  </div>
</template>

<script>
  import cheerio from 'cheerio'
  import http from 'http'
  import iconv from 'iconv-lite'
  import axios from 'axios'
  export default ({
    data() {
      return {
        articles: [],
        moviesTitle : [],
      }
    },
    created() {
    },
    mounted() {
      var html = '';
      for(let i = 0; i < 10; i++){
        for(let y = 0; y <= i; y ++){
          if(y == i){
            html += i + " * " + y + " = " + ( i * y );
          }else {
            html += i + " * " + y + " = " + ( i * y ) + ',  ';
          }
        }
        html +="\n"
      }
      console.log('\n' + html)

      //
      this.fetchData();
      //Json
      this.showLocaljson();
      //Json
      this.douBanJson();
    },
    methods: {
      fetchData() {
        const url = '/ygdy';
        let titles = [];
        http.get(url + '/html/gndy/dyzz/index.html', function (sres) {
          var chunks = [];
          sres.on('data', function (chunk) {
            chunks.push(chunk);
          });
          // chunks html zhuan ma cheerio.load 
          //  jQuery  `$`
          //  jQuery 
          sres.on('end', function () {
            //gb2312
            //<meta http-equiv="Content-Type" content="text/html; charset=gb2312">
            var html = iconv.decode(Buffer.concat(chunks), 'gb2312');
            var $ = cheerio.load(html, {
              decodeEntities: false
            });
            $('.co_content8 .ulink').each(function (idx, element) {
              var $element = $(element);
              titles.push({
                title: $element.text()
              })
            })
          });
        });
        this.moviesTitle = titles;
      },
      getImage(url){
        if(url !== undefined){
          var srcUrl = url.split('/');
          srcUrl = '/static/images/' + srcUrl[srcUrl.length - 1];
          return srcUrl;
        }
      },
      showLocaljson() {
        axios.get('/static/json/index.json').then(response => {
        }, response => {
          // error callback
        })
      },
      douBanJson() {
        const apiUrl = '/api';
        axios.get(apiUrl + '/movie/top250?count=10').then(response => {
          //console.log(response)
          this.articles = response.data.subjects;
        }, response => {
          // error callback
        })
      },
      getPromise(){
        const promise = new Promise(function (resolve, reject) {
          const image = new Image();
          image.onload = function () {
            resolve(image)
          };
          image.onerror = function () {
            reject(new Error('could not load image'))
          }
        })
      }
    }
  })
</script>
<style scoped="">
  .getJsonDemo {
    display: flex;
    flex-direction: row;
  }
  .getJsonDemo li {
    margin: 30px 5px 0 5px;
  }
  .getJsonDemo li img {
    width: 100%;
  }
</style>



